<template>
    <button class="btn"
            @click="handleClick"
            :disabled="disabled"
    >
        {{btnTxt}}
    </button>
</template>

<script>
    export default {
        name: 'linearButton',
        props: {
            btnTxt: {
                type: String,
                default: ''
            },
            disabled: {
                type: Boolean,
                default: false
            }
        },
        methods: {
            handleClick () {
                this.$emit('click')
            }
        }
    }
</script>

<style lang="scss" scoped>
    .btn{
        background:linear-gradient(124.6deg,rgba(253,58,4,1),rgba(242,147,0,1));
        border: none;
        outline: none;
        height: px2rem(90);
        width: 100%;
        line-height: px2rem(90);
        color: #ffffff;
        border-radius: px2rem(45);
        &:active{
            background:linear-gradient(124.6deg,rgba(221,26,0,1),rgba(210,115,0,1));
        }
        &[disabled]{
            background:linear-gradient(124.6deg,#C7C8CF,#DBDCE1);
        }
    }
</style>



