<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>忘记密码</h1>
            </v-header>
        </div>
        <div class="forgetPwd-container">
            <div class="cell-wrap">
                <v-cell-box>
                    <span class="title" slot="left">手机号</span>
                    <input type="tel"
                           placeholder="请输入手机号"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.phone"
                           maxlength="11"
                    >
                </v-cell-box>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="下一步" :disabled="!canNext" @click="jumpNext"></v-linear-button>
            </div>
        </div>
    </v-page>
</template>

<script>
    import {vCellBox} from '../../component/cell'
    import {vLinearButton} from '../../component/linearButton'
    export default {
        name: 'forgetPwd',
        components: {
            vCellBox,
            vLinearButton
        },
        computed: {
            canNext () {
                return this.forgetInfo.phone !== ''
            }
        },
        data () {
            return {
                forgetInfo: {
                    phone: ''
                }
            }
        },
        methods: {
            // 跳转到密码设置页面 分为为绑卡和绑卡页
            jumpNext () {
                let randNum = this.getRandomNum()
                randNum > 0.5 ? this.jumpCheckIdentity() : this.jumpSetPwd()
            },
            // 跳转已绑卡设置密码页面
            jumpCheckIdentity () {
                let jumpUrl = '/login/forgetPwd/checkIdentity'
                let params = this.forgetInfo
                window.T.go({
                    path: jumpUrl,
                    params
                })
            },
            // 跳转未绑卡设置密码页面
            jumpSetPwd () {
                let jumpUrl = '/login/forgetPwd/setPwd'
                let params = Object.assign(this.forgetInfo, {hasBindCard: false})
                window.T.go({
                    path: jumpUrl,
                    params
                })
            },
            // 生成个随机数
            getRandomNum () {
                return Math.random()
            }
        }
    }
</script>

<style lang="scss" scoped>
    .forgetPwd-container {
        padding: px2rem(20) 0 px2rem(40) 0;
        .cell-wrap {
            font-size: px2rem(32);
            span {
                display: block;
            }
            .title {
                width: px2rem(160);
                margin-right: px2rem(50);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
        }
        .btn-wrap {
            padding: 0 px2rem(40);
            margin-top: px2rem(80);
        }
    }
</style>



