<template>
    <div class="cell-box"
         :class="{ 'cell-box-border': hasBorder, 'cell-box-active': hasActive, 'cell-box-bg': hasBg }">
        <div class="left">
            <slot name="left"></slot>
        </div>
        <div class="middle">
            <slot name="middle"></slot>
        </div>
        <div class="right">
            <slot name="right"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'uiCell',
        props: {
            // 是否有下边框
            hasBorder: {
                type: Boolean,
                default: false
            },
            // 是否有点击效果
            hasActive: {
                type: Boolean,
                default: false
            },
            // 是否有背景颜色
            hasBg: {
                type: Boolean,
                default: true
            }
        }
    }
</script>

<style lang="scss" scoped>
    .cell-box{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: px2rem(100);
        padding: 0 px2rem(28);
        .left{
            flex: none;
        }
        .middle{
            flex: 1;
        }
        .right{
            flex: none;
        }
    }
    .cell-box-bg{
        background-color: #ffffff;
    }
    .cell-box-border{
        border-bottom: 1px solid #DBDBDB;
    }
    .cell-box-active:active{
        @include bg(#ffffff)
    }
</style>



